@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@500;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body * {
  font-family: 'Manrope', sans-serif;
}

div.nav-bar {
  background-color: #FDD835;
  color: #757575;
  display: flex;
  justify-content: space-between;
  padding: 5px;
}

div.nav-bar img {
  height: 20px;
  margin: 10px;
}

.nav-bar ul {
  display: flex;
  margin: 10px 0;
}

ul > li {
  display: block;
  text-decoration: none;
  list-style: none;
  margin-right: 15px;
}

ul > li:last-child {
  margin-right: 0;
}

button {
  height: 40px;
  width: 200px;
  border-radius: 4px;
  border: solid 1px white;
  margin: 0 15px;
}

button > span::before {
  content: "\f007";
}
nav > ul {
    display: flex;
    justify-content: center;
}

nav > ul li {
    margin-right: 35px;
}

nav > ul a {
    padding: 10px 15px;
    text-decoration: none;
    color: #000;
}

nav > ul a:hover {
    background-color:#FDD835;
    color: white;
}

nav > ul li:last-child {
    margin-right: 0;
}

.profile-tab {
    display: flex;
    padding: 40px;
}

.profile-tab > nav > ul {
    flex-direction: column;
    justify-content: space-between;
    padding-left: 0px;
}

.profile-tab > nav li {
    margin-bottom: 75px;
}
div.statistics {
    padding-left: 30px;
    display: flex;
    flex-direction: column;
}

canvas {
    border: solid 1px #FDD835;
}

.graphic-card {
    margin: 15px;
    width: 700px;
    border-radius: 5px;
}

.graphic-title {
    padding: 15px;
    margin: 0;
    border-radius: 5px 5px 0 0;
    border-bottom: solid 1px #757575;
}

.graphic-diagram {
    padding: 15px;
    border-radius: 0 0 5px 5px;
}

